.App {
  text-align: center;
  color: white;
  background-color: #252429;
}

.app__header {
  padding: 50px;
}

.app__desc > h2 {
  color: #e42d42;
}

.app__desc > p {
  font-size: 20px;
  padding: 10px;
}

.app__desc > p > a {
  color: white;
}

.user {
  width: 300px;
  border: 1px solid #f74700;
  border-radius: 10px;
  padding: 30px;
  display: flex;
  align-items: center;
  height: 100px;
}

.users {
  padding: 50px;
  display: flex;
  justify-content: center;
}

.users > .user {
  display: none;
}

.user__details > h1 {
  font-size: 25px;
}

.profiles {
  display: grid;
  grid-gap: 30px;
  grid-template: 150px/ 362px 362px 362px;
}

.profiles > .user > div > a > img {
  width: 40px;
  margin-left: 40px;
}

@media only screen and (max-width: 600px) {
  .profiles {
    display: flex;
    flex-direction: column;
  }

  .user {
    padding: 20px;
  }

  .app__desc {
    max-width: 80%;
  }
}
